@media only screen and (max-width: 768px) {
    .about-main-container {
        display: block;
        margin: 0 auto;
        width: 75%;
        margin-top: 2rem;
    }
    
    .about-sub-container {
        display: flex;
        flex-basis: 50%;
        flex-flow: column wrap;
        align-items: center;
        margin-bottom: 2rem;
    }
    
    .about-main-body {
        display: flex;
        flex-flow: column;
        align-items: center;
    }

    .about-main-body p {
        color: gray;
    }
    
    .about-sub-body p, ul {
        color: gray;
    }

    h2 {
        font-size: 1.2rem;
    }

    h3 {
        font-size: 1.2rem;
    }
}

@media only screen and (min-width: 769px) {
    .about-main-container {
        display: block;
        margin: 0 auto;
        width: 50%;
        margin-top: 2rem;
    }
    
    .about-sub-container {
        display: flex;
        flex-basis: 50%;
        flex-flow: column wrap;
        align-items: center;
        margin-bottom: 2rem;
    }
    
    .about-main-body {
        display: flex;
        flex-flow: column;
        align-items: center;
    }
    
    .about-sub-body p, ul {
        color: darkgray;
    }

    .about-main-body p {
        color: darkgray;
    }
}




