a {
    text-decoration: none;
    color: black;
    font-weight: bold;
}


@media only screen and (max-width: 768px) {
    .main-portfolio-wrapper {
        margin-top: 4rem;
        margin-bottom: 4rem;
        min-height: 100%;
        display: flex;
        justify-content: center;
    }
    
    .sub-portfolio-wrapper {
        display: flex;
        flex-flow: row wrap;
        flex-basis: 100%;
        justify-content: space-around;
    }
    
    .portfolio-row {
        display: flex;
        flex-flow: row wrap;
        justify-content: space-around;
    }
    
    .portfolio-project {
        flex-basis: 75%;
        height: 12rem;
        margin-bottom: 2rem;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
        border-radius: 2px;
        position: relative;
        text-align: center;
        transition: visibility 0s, opacity .5s linear;
        background-color: transparent;
    }
    
    .portfolio-project-middle {
        visibility: hidden;
        opacity: 0;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%)
    }
    
    .portfolio-project-middle-text {
        border: 2px solid black;
        width: 75%;
        height: auto;
        padding: 1rem;
    }
    
    .portfolio-project-middle-text:hover {
        cursor: pointer;
    }
    
    .portfolio-project:hover .portfolio-project-middle{
        visibility: visible;
        opacity: 1;
    }
    
    .resume-button {
        position: absolute;
        left: 0%;
        top: 5%;
        height: 2.5rem;
        width: 2.5rem;
        padding: .2rem;
        border-radius: 180px;
        background-color: #A8C0CE;
        box-shadow: 5px 5px 10px gray;
        display: flex;
        flex-flow: row;
        align-items: center;
    }
    
    .animation {
        animation-name: bounce;
        animation-duration: 1s;
        animation-timing-function: linear;
        animation-iteration-count: 3;
    }
    
    @keyframes bounce {
        0%, 100% {
            transform: translateY(0)
        }
        50% {
            transform: translateY(10px);
        }
        
    }
    
    .resume-button p {
        font-weight: bold;
        text-align: center;
        background-color: none;
        font-size: .7rem;
    }
    
    .animation p:hover {
        cursor: pointer;
        color: blue;
    }
    
    section:hover .middle {
        opacity: 1;
    }
    
    .middle {
        transition: .5s ease;
        opacity: 0;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
        display: block;
        margin: 0 auto;
    }
    
    section:hover img {
        opacity: .3;
    }
    
    .hidden {
        display: none;
    }
    
    .transluscent {
        opacity: 5%;
        pointer-events: none;
    }
    
    .resume-container {
        position: absolute;
        left: 8%;
        width: 22rem;
        height: 30rem;
        opacity: 100%;
       
    }
    
    .resume {
        width: 22rem;
        height: 30rem;
        opacity: 100%;
    }
    
    .portfolio-img {
        max-height: 100%;
        width: auto;
    }
    
    .resume-download {
        text-decoration: none;
        font-weight: bold;
        color: black;
    }

}

