a {
    text-decoration: none;
    font-weight: bold;
    color: black;
}

@media only screen and (max-width: 768px) {

    .home-wrapper-main {
        display: flex;
        flex-flow: column;
        justify-content: center;
        align-items: center;
        max-height: 100vh;
        width: auto;
        margin: 3rem 10px;
    }

    .home-wrapper-sub {
        display: flex;
        flex-flow: column;
        align-items: center;
        justify-content: center;
        width: 75%;
    }

    .title-line {
        display: flex;
        flex-flow: column nowrap;
        align-items: center;
        justify-content: flex-start;
        font-size: .7rem;
    }

    .home-wrapper-sub p {
        color: gray;
    }

    .home-project-img {
        height: 10rem;
        width: auto;
    }

    .home-project {
        height: 10rem;
        flex-basis: 75%;
        box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
        margin: 3rem 0;
        position: relative;
        text-align: center;
        transition: visibility 0s, opacity .5s linear;
    }

    .home-wrapper-projects {
        display: flex;
        flex-flow: column wrap;
        align-items: center;
        justify-content: space-around;
        width: 100%;
        align-items: center;
    }

    .home-project-middle {
        visibility: hidden;
        opacity: 0;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%)
    }
    
    .home-project-middle-text {
        border: 2px solid black;
        width: 75%;
        height: auto;
        padding: 1rem;
    }

    
    .home-project-middle-text:hover {
        cursor: pointer;
    }
    
    .home-project:hover .home-project-middle{
        visibility: visible;
        opacity: 1;
    }

} 

@media only screen and (min-width: 769px) {
    .home-wrapper-main {
        display: block;
        width: 50%;
        margin: 0 auto;
        margin-bottom: 5rem;
    }
    
    .home-wrapper-sub {
        display: flex;
        flex-flow: column;
        align-items: center;
        margin-bottom: 3rem;
    }
    
    .home-wrapper-sub p {
        color: gray;
    }

    
    .title-line {
        display: flex;
        flex-flow: column nowrap;
        align-items: center;
        justify-content: flex-start;
    }
    
    .title-line h2 {
        margin-top: 0;
        margin-bottom: 2rem;
    }
    
    .home-wrapper-projects {
        display: flex;
        flex-flow: column wrap;
        justify-content: space-around;
        width: 100%;
        align-items: center;
    }
    
    .home-project {
        height: 20rem;
        box-shadow: 5px 5px 10px gray;
        margin: 3rem 0;
        position: relative;
        text-align: center;
        transition: visibility 0s, opacity .5s linear;
    }
    
    .home-project-middle {
        visibility: hidden;
        opacity: 0;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%)
    }
    
    .home-project-middle-text {
        border: 2px solid black;
        width: 100%;
        height: auto;
        padding: 1rem;
    }
    
    .home-project-middle-text:hover {
        cursor: pointer;
    }
    
    .home-project:hover .home-project-middle{
        visibility: visible;
        opacity: 1;
    }
    
    .home-project-img {
        max-height: 100%;
        width: auto;
    }
}

