body {
    background-color: white;
    font-family: 'Roboto', sans-serif;
}


@media only screen and (max-width: 768px) {

    .main {
        min-height: 100vh;
    }

    Footer {
        position: relative;
        bottom: 0%;
        right: 5%;
    }
}

@media only screen and (min-width: 769px) {
    .main {
        min-height: 100vh;
        display: flex;
        flex-flow: column;
        
    }
}



